<template>
  <div id="system">
    <div class="title">系统设置</div>
    <div class="content" v-if="systemShow">
      <div class="top">
        <div class="left">管理员设置</div>
        <div class="right" @click="systemShow=false">添加管理员</div>
      </div>
      <div class="line"></div>
      <el-table
          :data="tableData"
          class="table">
        <el-table-column
            prop="userName"
            label="姓名"
            align="center"
            mid-width="20%">
        </el-table-column>
        <el-table-column
            prop="userId"
            label="账号"
            align="center"
            mid-width="20%">
        </el-table-column>
        <el-table-column
            prop="password"
            label="密码"
            align="center"
            mid-width="20%">
        </el-table-column>
        <el-table-column
            label="管理员类型"
            align="center"
            mid-width="20%">
          <template slot-scope="scope">
            {{  scope.row.identity?'超级管理员':'未知' }}
          </template>
        </el-table-column>
        <el-table-column
            label="操作"
            align="center"
            mid-width="20%">
          <template slot-scope="scope">
            <el-button @click="getDelUser(scope.row)" type="text" style="color: red;">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="add" v-else>
      <div class="top">
        <div class="left">管理员信息</div>
      </div>
      <el-form ref="form" :model="userForm" label-width="60px" class="form">
        <el-form-item label="姓名">
          <el-input v-model="userForm.userName" placeholder="请输入管理员姓名" class="input"></el-input>
        </el-form-item>
        <el-form-item label="账号">
          <el-input v-model="userForm.userId" placeholder="请输入管理员账号" class="input"></el-input>
        </el-form-item>
        <el-form-item label="密码">
          <el-input v-model="userForm.password" placeholder="请输入管理员密码" class="input"></el-input>
        </el-form-item>
      </el-form>
      <div class="btnBox">
        <div class="btn" @click="systemShow=true">取消</div>
        <div class="btn" @click="checkUser()">确认</div>
      </div>
    </div>
    <el-dialog
        title="确认删除管理员"
        :visible.sync="dialogVisible1"
        width="25%"
        class="dialog"
        :lock-scroll="false"
        top="30vh"
        center>
      <div class="tips">
        删除后该账号将无法登陆，是否删除？
      </div>
      <div slot="footer" class="dialog-footer">
        <div class="btn" @click="dialogVisible1 = false">取消</div>
        <div class="btn" @click="deleteUser()">确认</div>
      </div>
    </el-dialog>
    <el-dialog
        title="确认添加管理员"
        :visible.sync="dialogVisible2"
        width="25%"
        class="dialog"
        :lock-scroll="false"
        top="30vh"
        center>
      <div class="tips">
        添加管理员后该账号将可登陆并对网站后台进行操作，是否确认添加？
      </div>
      <div slot="footer" class="dialog-footer">
        <div class="btn" @click="dialogVisible2 = false">取消</div>
        <div class="btn" @click="addUser()">确认</div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "System",
    data() {
      return {
        systemShow: true,
        dialogVisible1: false,
        dialogVisible2: false,
        tableData: [],
        userForm: {
          userName: '',
          userId: '',
          password: ''
        },
        deleteId: ''
      }
    },
    methods: {
      getUser() {
        this.$axios.get('/user/user',).then((res) => {
          if(res.status === 200) {
            this.tableData = res.data;
          }
          else {
            this.$message.error(res.msg);
          }
        }).catch((err) => {
          console.log(err);
        })
      },
      addUser() {
        var userInfo = this.userForm;
        this.$axios.put('/user/user',{
          userName: userInfo.userName,
          userId: userInfo.userId,
          password: userInfo.password
        }).then((res) => {
          if(res.status === 200) {
            this.getUser();
            this.dialogVisible2 = false;
            this.systemShow = true;
            this.successMsg();
          }
          else {
            this.dialogVisible2 = false;
            this.$message.error(res.msg);
          }
        }).catch((err) => {
          console.log(err);
        })
      },
      getDelUser(row) {
        this.deleteId = row.userId;
        this.dialogVisible1 = true;
      },
      deleteUser() {
        this.$axios.delete('/user/user',{
          data: {
            userId: this.deleteId
          }
        }).then((res) => {
          if(res.status === 200) {
            this.dialogVisible1 = false;
            this.successMsg();
            this.getUser();
          }
          else {
            this.$message.error(res.msg);
          }
        }).catch((err) => {
          console.log(err);
        })
      },
      checkUser() {
        this.dialogVisible2 = true;
      },
      successMsg() {
        this.$message.success('操作成功！');
      },
      clearForm() {
        this.userForm.userName = '';
        this.userForm.userId = '';
        this.userForm.password = '';
      }
    },
    beforeMount() {
      this.getUser();
    },
    beforeRouteLeave(to,from,next) {
      this.clearForm();
      this.systemShow = true;
      next();
    }
  }
</script>

<style lang="less" scoped>
  @import "../../assets/style/system/system";
</style>
